:root {
    --filter-width:345px;
  }

body {
overflow-x: hidden;
}
.search-container {
    /* margin-top:64px; */
    width: 100%;
    height: 81px;
    background: #FAFAFA;
}

.search-bar {
    width: 100%;
    height: 41px;
    margin-top:18px;
    display:flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    border: 1px solid #D0D0D0;
    box-sizing: border-box;
    border-radius: 4px;
}

.search-bar:focus {
    border:2px solid black;
}

#search-icon {
    margin-left:10px;
    margin-right:10px;
    
}
#search-input {
    width:95%;
    height:41px;
    border: 1px solid #D0D0D0;
    border-left:none;
}
#search-input:focus .search-bar {
    border:2px solid #15BF3A;
}
#green-line {
    width:100%;
    height:0px;
    border: 2px solid #15BF3A;
}

.filter-container {
    position:absolute;
    top:160px;
    display:flex;
    flex-direction:column;
    flex-wrap:wrap;
    align-items:center;
    width: var(--filter-width);

}

#filter-btn {
    border-radius: 4px;
    width:160px;
    height:48px;
    line-height: normal;
    margin-top:30px;

}

.line-separator {
    width:40%;
    height:0px;
    border: 1px solid #000000;
    margin-top:24px;
}

#cuisine-header {
    font-size:24px;
    font-weight:400;
    margin-right:70px;
    margin-top:30px;
}
.cuisine-boxes {
    width:160px;
    margin-top:30px;
    font-size:18px;
}
.cuisine-labels {
    margin-left:10px;
}

.cuisine-input {
    margin-bottom:5px;
}

#type-header {
    font-size:24px;
    font-weight:400;
    margin-right:100px;
    margin-top:30px;
}
.type-boxes {
    width:160px;
    margin-top:30px;
    font-size:18px;
}
.type-labels {
    margin-left:10px;
}

.type-input {
    margin-bottom:5px;
}

#location-header {
    font-size:24px;
    font-weight:400;
    margin-right:60px;
    margin-top:30px;
}

#location-dropdown {
    background-color: white;
    border: 1px solid black;
    width:150px;
    margin-top:20px;
    margin-right:10px;
    
}

#dropdown-container {
    margin-top:20px;
    height:50px;
    display:flex;
    align-items: center;

}

#dropdown {
    background: #CBCBCB;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    border:none;
    font-weight:600;
}

#filter-box {
    margin-right:30px;
}

#browse-container {
    width:100%;
    overflow:visible;
}

#slider {
    display: flex;
    overflow-x:scroll;
    scroll-behavior: smooth;
    transition:0.5s;
}

#slider-all {
    display: flex;
    flex-wrap:wrap;
    /* overflow-x:scroll; */
    scroll-behavior: smooth;
    transition:0.5s;
}

#slider::-webkit-scrollbar {width: 0 !important;
    display: none; }

#card-container {
    /* box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);  */
    margin-bottom: 15px;
    border-radius: 4px;
    transition:1s;
    height:250px;
    width:270px;
    flex-shrink: 0;
    margin-right:25px;
}

#card-container:hover {
    cursor: pointer;

}
#card-img-slider {
    height:150px;
}
#card-img {
    object-fit: cover;
    height:150px;
}
#card-container:hover #card-img{
    opacity:0.5;
}
#card-title {
    white-space: nowrap;
overflow: hidden;
text-overflow: ellipsis;
    font-weight:700;
}

#card-price {
    margin-top:5%;
    padding:none;
}

#card-time {
    margin-top:-40px;
    float:right;
}

.browse-row {
    display:flex;
    
}

.category-container {
    margin-top:30px;
}

#category-header {
    height:64px;
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.arrows {
    display:flex;
    align-items:center;
    border-radius: 20px;
    padding-bottom:6px;
}

.arrow-left {
    display:flex;
    align-items:center;
    justify-content: center;
    width:32px;
    height:32px;
    border-radius: 20px;
    background:#e5e5e5;
}

.arrow-left:hover {
    border-radius: 20px;
    background:#f5f5f5;
    
}
.arrow-right {
    margin-left:10px;
    display:flex;
    align-items:center;
    justify-content: center;
    width:32px;
    height:32px;
    border-radius: 20px;
    background:#e5e5e5;
}

.arrow-right:hover {
    border-radius: 20px;
    background:#f5f5f5;
    
}

#card-arrow-left{
    position:absolute;
    z-index:99;
    top:65px;
    opacity:0.5;

}

#card-arrow-right{
    position:absolute;
    top:65px;
    right:0;
    z-index:99;
    opacity:0.5;
}


#see-all {
    color:rgb(114, 114, 114);
    width:64px;
    font-weight:600;
    /* padding-right:6px; */
}

#see-all:hover {
    cursor:pointer;
}
#category-title {
    font-size: 24px;
    width:100%;
    font-weight:700;
}
#off {
    display:none;
}
@media(max-width: 750px) {
    .search-bar {
        margin-left:0px;
    }
    .filter-container {
        display: none;
    }
    #browse-container {
        left:0px;
        width:100vw;
    }
    #dropdown-container {
        left:0px;
        width:100vw;
    }
}